.App {
  text-align: center;
  background-color: #2C3E50;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.clock-container {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 4px solid #ECF0F1; /* Light grey border */
  background-color: #34495E; /* Slightly lighter dark blue background */
}

.number {
  position: absolute;
  transform: translate(-50%, -50%);
  font-size: 18px;
  color: #ECF0F1; /* Light grey numbers */
}

.hour-hand {
  background-color: #ECF0F1; /* Light grey for the hour hand */
}

.minute-hand {
  background-color: #ECF0F1; /* Light grey for the minute hand */
}

.second-hand {
  background-color: #E74C3C; /* Red for the second hand */
}
